<script>
import AbstractCrud from "@/components/abstract/crud"
import CrudForm from "./form"
import CrudTable from "./table"

import service from "@service/pessoa"

export default {
  extends: AbstractCrud,
  components: {
    CrudForm,
    CrudTable
  },
  computed: {
    service: {
      get() {
        return service
      }
    },
    title: {
      get() {
        return "Cadastro Pessoa"
      }
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  max-width: 1000px;
}
</style>




